<template>
  <div>
    <CinemaSearch class="div-search"></CinemaSearch>
    <CinemaFilterNav class="cinema-filter"
      :city-cinema-info="cityCinemaInfo"
      @change="changeCondition"
      @toggleShow="toggleShow"
    >
    </CinemaFilterNav>
    <!-- <CinemaList class="list" ref="list"></CinemaList> -->
    <van-list
      v-model="loading"
      :finished="finished"
      :offset="offset"
      :immediate-check="check"
      finished-text=""
      @load="onReachBottom"
    >
      <div v-for="(item, index) in cinemas" :key="index">
        <CinemaSection :cinema="item"></CinemaSection>
      </div>
    </van-list>
    <div v-show="nothing">
      <Empty message=""></Empty>
    </div>
  </div>
</template>

<script>
import CinemaSearch from '@/components/cinema/CinemaSearch.vue'
import CinemaFilterNav from '@/components/cinema/CinemaFilterNav.vue'
// import CinemaList from '@/components/cinema/CinemaList.vue'
import Empty from '@/components/Empty.vue'
import { getToday } from '@/utils/util.js'
import CinemaSection from '@/components/cinema/CinemaSection.vue'
import { List } from 'vant'
export default {
  data() {
    return {
      params: {
        day: getToday(),
        offset: 0,
        limit: 20,
        districtId: -1,
        lineId: -1,
        hallType: -1,
        brandId: -1,
        serviceId: -1,
        areaId: -1,
        stationId: -1,
        item: "",
        updateShowDay: false
      },
      cityCinemaInfo: { // Nav
        brand: {
          subItems: ""
        },
        service: {
          subItems: ""
        },
        hallType: {
          subItems: ""
        }
      },
      nothing: false, // 
      cinemas: [], // 
      loading: false, // load
      finished: false, // 
      offset: 200, //  offset load
      check: false // 
    }
  },
  components: {
    CinemaSearch,
    CinemaFilterNav,
    // CinemaList,
    CinemaSection,
    Empty,
    [List.name]: List
  },
  created() {
    this.$store.commit("changeTitle","");
    // this.getCinemaList(this.params);
    this.initPage();
  },
  methods: {
    async initPage() {
      const filter = await this.getFliterCinemas();
      this.cityCinemaInfo = filter.data;
      const list = await this.getCinemaList(this.params);
      this.cinemas = [...list.data.cinemas];
      this.finished = !list.data.paging.hasMore;
    },
    // 
    getCinemaList(params) {
      return this.$request.get("/cinemas/cinemaList",params);
    },
    getFliterCinemas() {
      return this.$request.get("/cinemas/filterCinemas");
    },
    // 
    onReachBottom() {
      const params = {...this.params,offset:this.cinemas.length}; // ,offsetparamsoffset
      if(this.finished) {
        this.loading = false;
      } else {
        this.loading = true;
        this.getCinemaList(params).then(res => {
          this.cinemas = [...this.cinemas,...res.data.cinemas];
          this.finished = !res.data.paging.hasMore;
          // check,
          setTimeout(() => {
            this.loading = false;
          },500);
        })
      }
    },
    // 
    changeCondition(obj) {
      this.params = {
        ...this.params,
        ...obj
      };
      this.cinemas = [];
      this.nothing = false;
      this.getCinemaList(this.params).then(list => {
        if(!list.data.cinemas.length) {
          this.nothing = true;
        }
        this.cinemas = [...this.cinemas,...list.data.cinemas];
        this.finished = !list.data.paging.hasMore;
      })
    },
    // 
    toggleShow(e) {
      
    }
  }
}
</script>

<style scoped>
.div-search {
  position: fixed;
  width: 100%;
  /* margin-top: 50px; */
  top: 46px;
}
.cinema-filter {
  position: fixed;
  top: 85px;
}
.list {
  margin-top: 75px;
}
.van-list {
  padding-top: 70px;
} 
</style>
